import "../styles.css";

import React, { useState, useEffect } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";

function App() {
  const [noteList, setNoteList] = useState(
    JSON.parse(localStorage.getItem("notes-app")) || []
  );

  console.log(localStorage.getItem("notes-app"));

  useEffect(() => {
    localStorage.setItem("notes-app", JSON.stringify(noteList));
  }, [noteList]);

  // useEffect(() => {
  //   window.addEventListener("storage", () => {
  //     setNoteList(JSON.parse(localStorage.getItem("notes-app")));
  //   });
  // }, [noteList]);

  function addNote(myNote) {
    setNoteList((prevList) => {
      return [...prevList, myNote];
    });
  }

  function removeNote(id) {
    setNoteList((prevList) => {
      return prevList.filter((item, index) => {
        return index !== id;
      });
    });
  }

  function updateNote(currentNote, id) {
    setNoteList((prevList) => {
      prevList.splice(id, 1, currentNote);
      return prevList;
    });
    localStorage.setItem("notes-app", JSON.stringify(noteList));
    console.log(noteList);
  }

  return (
    <div>
      <Header />
      <CreateArea newNote={addNote} />
      {noteList.map((note, index) => (
        <Note
          key={index}
          id={index}
          title={note.title}
          content={note.content}
          delete={removeNote}
          update={updateNote}
        />
      ))}
      <Footer />
    </div>
  );
}

export default App;
